              @change="e=>handleChange(e,item)"
            >
              <el-checkbox
                v-for="(option,i) in item.options"
                :label="option.label"
                :key="option.label"
                :class="{'check-box-hide':hide}"
                :style="`transition:all 0.5s ease-in-out ${1+i/10}s;${option.label.length>14?'text-align:center':''}`"
