        account: 'a62275218',
        token: '66d76e593a3ca3541f18f092aef999f8',
        onconnect: function () {
          console.log('SDK')
        },
        onerror: function (err) {
          console.log(err)
        },
        oncustomsysmsg: function (sysMsg) {
          console.log('', sysMsg)
