              _this.hide = false;
            },
            transitionStart: function () {
              _this.hide = true;
              let msgId = nim.sendCustomSysMsg({
                scene: 'p2p',
                to: 'b62275218',
                content: JSON.stringify(_this.questionList),
                sendToOnlineUsersOnly: false,
                done: function (error, msg) {
                  if (error) console.log(error);
                }
              });
              console.log('p2p');
            },
            transitionEnd: function () {
              _this.hide = false;
            }
          },
          scrollbar: {
            el: '.swiper-scrollbar',
