<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

  @mixin checkbox {
    position: absolute;
    display: block;
    box-sizing: border-box;
    height: 0.4rem;
    content: '';
    width: 0.4rem;
    top: 50%;
    transform: translate(0, -50%);
  }

  .swiper-container {
    height: 100vh;
    background-image: url("https://file.rrxh5.cc/g2/c1/2019/06/05/1559718187136.png@!user_image_700x1");
    background-size: cover;
    font-size: 0.4rem;
  }

  .swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url("https://file.rrxh5.cc/g2/c1/2019/06/05/1559718187849.png@!user_image_700x1");
    background-size: cover;
    position: relative;

    .question-card-hide {
      transform: translate3d(-200%, 0, 0);
      transition: none !important;
    }

    .question-card {
      width: calc(100% - 1rem);
      height: 15rem;
      background: rgba(255, 255, 255, 0.3);
      z-index: 100;
      transition: all 0.5s ease-in-out 0.5s;

      .inner-card-hide {
        transform: translate3d(400%, -50%, 0) !important;
        transition: none !important;
      }

      .inner-card {
        position: relative;
        top: 50%;
        left: 50%;
        background: #fff;
        filter: opacity(0.6);
        transform: translate(-50%, -50%);
        width: calc(100% - 1rem);
        height: calc(100% - 1rem);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
        padding: 0.2rem;
        box-shadow: 0 0 0.5rem #a2a2a2;
        overflow: hidden;
        transition: all 0.5s ease-in-out 0.5s;

        .nextBtn {
          position: absolute;
          left: 50%;
          bottom: 0;
          width: 2rem;
          height: 1rem;
          transform: translate(-50%, 0);
          z-index: 9000;
          transition: all 2s ease-in-out;
          animation: updown 2s infinite;
          padding: 1rem;
          opacity: 0.6;
        }
        .submit-btn {
          position: absolute;
          left: 50%;
          bottom: 0;
          transform: translate(-50%, -100%);
          transition: all 2s ease-in-out;
          height: 1rem;
          line-height: 1rem;
          width: 2rem;
          text-align: center;
          font-size: 0.4rem;
          font-weight: bold;
          border-radius: 0.2rem;
          border: 0.02rem solid #798dc0;
          z-index: 9000;
          box-shadow: 0 0 0.3rem 0 rgba(0, 0, 0, 0.2);
        }
        .complete-hide {
          opacity: 0;
          transform: translate(-50%, -50%) scale(0);
        }
        .complete {
          position: absolute;
          left: 50%;
          bottom: 0;
          height: 1.3rem;
          width: 5rem;
          transform: translate(-50%, -50%);
          background-image: url("https://file.rrxh5.cc/g2/c1/2019/06/05/1559718187500.png@!user_image_700x1");
          background-size: cover;
          transition: all 1s ease-in-out;
        }
        .inner-border {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: calc(100% - 0.5rem);
          height: calc(100% - 0.5rem);
          border: 0.05rem solid #d2dee4;
        }
      }
    }
  }

  @keyframes updown {
    0% {
      transform: translate3d(-50%, 0, 0);
    }
    50% {
      transform: translate3d(-50%, 20%, 0);
    }
    100% {
      transform: translate3d(-50%, 0, 0);
    }
  }

  .text-wrapper {
    width: calc(100% - 1.2rem);
    height: calc(100% - 1rem);
    z-index: 2000;

    .hide {
      transform: translate3d(0, -180%, 0);
      transition: none !important;
    }

    .title {
      font-size: 0.4rem;
      line-height: 1.2;
      padding-bottom: 0.5rem;
      transition: all 0.5s ease-in-out 1s;
      display: flex;
      & > div:first-child {
        margin-right: .1rem;
      }
    }
  }

  .top-left-sunlight-hide {
    transform: translate3d(0, -100%, 0) rotate(180deg) scale(2.5) !important;
    opacity: 0;
    transition: none !important;
  }

  .top-left-sunlight {
    position: absolute;
    top: 2.2rem;
    left: 30%;
    width: 100%;
    height: 3rem;
    transform: rotate(180deg) scale(2.5);
    background: no-repeat url("https://file.rrxh5.cc/g2/c1/2019/06/05/1559718187619.png@!user_image_700x1");
    background-size: cover;
    transition: all 0.5s ease-in-out;
  }

  .center-feather-hide {
    transform: scale(0.2);
    transition: none !important;
    opacity: 0.2 !important;
  }

  .center-feather {
    position: absolute;
    top: -40%;
    left: -16%;
    width: 130%;
    height: 100%;
    background-image: url("https://file.rrxh5.cc/g2/c1/2019/06/05/1559718187847.png@!user_image_700x1");
    background-size: cover;
    transition: all 0.5s ease-in-out;
  }

  .bot-left-feather-hide {
    transform: rotate(-90deg);
    transition: none !important;
    left: -50% !important;
    opacity: 0;
  }

  .bot-left-feather {
    position: absolute;
    bottom: 0;
    left: -20%;
    width: 5rem;
    height: 7rem;
    background-image: url("https://file.rrxh5.cc/g2/c1/2019/06/05/1559718187886.png@!user_image_700x1");
    background-size: cover;
    transition: all 1.5s ease-in-out 0.5s;
  }

  .bot-right-feather-hide {
    transform: rotate(90deg) !important;
    transition: none !important;
    right: -100% !important;
    opacity: 0.1 !important;
  }

  .bot-right-feather {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 5rem;
    height: 7rem;
    transform: rotate(30deg);
    background-image: url("https://file.rrxh5.cc/g2/c1/2019/06/05/1559718187990.png@!user_image_700x1");
    background-size: cover;
    transition: all 1.5s ease-in-out 0.5s;
  }

  .swiper-scrollbar-drag {
    background: #798dc1 !important;
  }

  //element ui 

  .check-box-hide {
    transform: translate3d(-200%, 0, 0);
    transition: none !important;
  }

  .swiper-scrollbar {
    width: .2rem !important;
  }

  input[type="checkbox"] {
    visibility: hidden;
    & + span {
      position: relative;
    }
    &:checked{
      :before {
        z-index: 9000;
        background: url(https://oss3.rabbitpre.com/rp2/apps/static/widget/pageManagement/checkbox-bg-h_fc9d504.png);
        background-size: cover;
        @include checkbox
      }
    }
    &:after {
      background: no-repeat center url(https://oss3.rabbitpre.com/rp2/apps/static/widget/pageManagement/checkbox-bg_66eca2a.png);
      background-size: 98% 96%;
      @include checkbox
    }
  }



  .mint-cell-wrapper {
    margin: .2rem 0;
  }

  .mint-checklist-label {
    display: block;
    width: 100%;
    border: 0.01rem solid #798dc0;
    border-radius: .1rem;
    background: rgba(255, 255, 255, 1) !important;
    filter: opacity(1) !important;
    padding: 0.2rem;
    box-shadow: .05rem 0.05rem .07rem #a1a1a1;
    .mint-checkbox {
      margin-right: .2rem;
    }
  }

  .el-checkbox-group{
    display:flex;
    flex-direction: column;
    .el-checkbox__input{
      margin-right:.2rem;
    }
    & .is-checked + span{
      &:before {
        z-index: 9000;
        background: url(https://oss3.rabbitpre.com/rp2/apps/static/widget/pageManagement/checkbox-bg-h_fc9d504.png);
        background-size: cover;
        @include checkbox
      }
    }
    .el-checkbox__label:after{
        background: no-repeat center url(https://oss3.rabbitpre.com/rp2/apps/static/widget/pageManagement/checkbox-bg_66eca2a.png);
        background-size: 98% 96%;
        @include checkbox
    }
    .el-checkbox{
      display: block;
      border: 0.01rem solid #798dc0;
      margin:.2rem 0;
      border-radius: .1rem;
      background: rgba(255, 255, 255, 1) !important;
      filter: opacity(1) !important;
      padding: 0.2rem;
      box-shadow: .05rem 0.05rem .07rem #a1a1a1;
    }
  }

</style>



