<template>
  <div class="navigator">
    <div class="title"></div>
    <div class="content">
      <div
        :class="['content-row',{'hide':!animate}]"
        v-for="(item,idx) in contentList"
        :style="`transition:all 1s ${idx}s ease-in-out`"
      >
        {{item}}
      </div>
      <div :class="['signature',{'hide':!animate}]">
        Developed by Jake
      </div>
    </div>
    <div :class="['confirm',{'hide':!animate}]" @click="navigateHome"></div>
  </div>
</template>

<script>
  export default {
    name: "Preload",
    data(){
      return {
        animate:false,
        contentList:[
          '',
          '',
          '',
          ''
        ]
      }
    },
    mounted(){
      setTimeout(()=>{
        this.animate = true
      },0)
    },
    methods:{
      navigateHome(){
        this.$router.push({path:'/home'})
      }
    }
  }
</script>

<style scoped>
  .navigator{
    font-family: "Qian Kun";
    position:absolute;
    top:50%;
    transform:translate(0,-50%);
    width:100vw;
    font-size:.5rem;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding:.4rem 0 2rem 0;
    color:#fff;
  }
  .title{
    margin:0 auto;
    font-size:.8rem;
  }
  .content{
    padding:.7rem;
  }
  .hide{
    opacity:0;
  }
  .signature{
    position:absolute;
    font-size:.5rem;
    bottom:0;
    right:.4rem;
    font-family: "Callie Hand";
    transition:all 1s ease-in-out 4s;
  }
  .content-row{
    padding:.2rem 0;
  }
  .confirm{
    margin-top:1rem;
    border:.01rem solid #fff;
    padding:.2rem;
    border-radius:.2rem;
    transition:all 1s ease-in-out 3s;
  }
</style>



